import React, { useState } from 'react'
import   './Navbar.css'
import { menuList } from '../../data/menuList'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'

export default function Navbar({openNav,setopenNav}) {


    const [colorNav, setcolorNav] = useState(false)
  return (
    <div className='Navbar'>


    


        <div className="logo">
            <Link to='/home'>            <p><i class="fa-solid fa-utensils"></i></p></Link>
            <p>makarona</p>
        </div>
        <div className="listMenu">

            {
                menuList.map((x,i)=>(
                    <Link className={(colorNav&&"activeMenuLI")} id='ulmenu' to={x.path}><li>{x.display}</li></Link>
                ))
            }
        </div>
        <div className="iconsNav">
              <Link id='cartd' to='/card'><i class="fa-solid fa-bag-shopping"></i></Link>

        <p className='shoppingCart'>

        <p><i class="fa-solid fa-user"></i></p>
        </p>
        </div>
        <div onClick={()=>setopenNav(!openNav)} className={"bars "+(openNav&&"activenav")}>
            <span></span><span></span><span></span>
        </div>
    </div>
  )
}
