import React from 'react'
import './List.css'
import { Link } from 'react-router-dom'

export default function List({setopenNav,openNav}) {
  return (
    <div className={'list '+(openNav&&"activeList")}>
        <ul>
          <Link id='linkList' to='/home'>
          <li onClick={()=>setopenNav(false)}>Home</li>
          </Link>

          <Link id='linkList' to='/popularFoods'>
          <li onClick={()=>setopenNav(false)}>product</li>
          </Link>

          <Link id='linkList' to='/card'>
          <li onClick={()=>setopenNav(false)}>cart</li>
          </Link>
          
        </ul>
    </div>
  )
}
